.data-table {
  width: 100%;
  max-width: 1000px;
  margin: auto;
  font-family: Arial, sans-serif;
}

@media (max-width: 600px) {
  .data-table {
    font-size: 80%;
  }
}

.data-table td {
  padding: .25em 0;
  text-align: center;
}

.data-table thead {
  font-size: 75%;
  font-weight: bold;
  text-align: center;
  z-index: 2;
}

.data-table thead th {
  padding: 1em 0;
  text-align: center;
}

.data-table thead th.sortable {
  cursor: pointer;
  color: royalblue;
}

.data-table thead th.sorted {
  background-color: royalblue;
  color: white
}

.data-table tbody tr {
  border-bottom: 1px solid silver;
}

.data-table tbody tr th {
  text-align: center;
  color: #777;
  font-size: 80%;
  font-weight: normal;
}

.data-table tbody tr th.th-label {
  font-weight: bold;
}

.data-table tbody tr td:first-child {
  border-left: 1px solid #eee;
}

.data-table tbody tr td:not(:last-child) {
  border-right: 1px solid #eee;
}

.data-table tbody td.em {
  background-color: #eee;
  font-weight: bold;
}



