.club-selector {
  background-color: white;
  width: 200px;
}

.club-selector-header {
  background-color: royalblue;
  height: 52px;
  text-align: center;
  color: white;
  font-weight: bold;
}

.club-selector-header .title {
  padding: .9em .2em;
}

.club-selector-header .loader {
  padding: .5em .2em;
  margin: auto;
}

.club-selector-header .header-button {
  position: absolute;
  top: .2em;
  right: .2em;
  font-size: 120%;
  background-color: rgba(255, 255, 255, 0.05);
  border: none;
  border-radius: 50%;
  padding: .5em .7em;
  color: white;
  -webkit-transition: background-color .25s;
  -o-transition: background-color .25s;
  transition: background-color .25s;
  -webkit-animation: fadeIn 1s;
          animation: fadeIn 1s;
}

.club-selector-header .header-button:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.club-selector h4 {
  text-align: center;
  font-size: 80%;
  font-family: Arial, sans-serif;
  font-weight: bold;
}

.club-selector ul {
  margin: auto;
  padding: 0;
}

.club-selector a li {
  text-align: center;
  list-style: none;
  padding: .8em .2em;
  cursor: pointer;
}

.club-selector ul a:hover {
  text-decoration: none;
}

.club-selector ul li:hover {
  background-color: #eee;
}

.club-selector a li.active {
  background-color: royalblue;
  color: white;
  font-weight: bold;
}

.club-selector a li.active:hover {
  background-color: royalblue;
  color: white;
  font-weight: bold;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity:0;
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    100% {
        opacity:1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes fadeIn {
    0% {
        opacity:0;
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    100% {
        opacity:1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



